<template>
    <q-dialog v-model="isOpen" :position="position" full-height persistent>
        <q-card class="column full-height" style="min-width: 600px; height: calc(100% - 72px);" >
            <q-card-section>
                <div style="display: flex; justify-content: space-between;">
                    <q-btn dense icon="close" flat @click="onClose" />
                    <div class="text-h6 text-center"> {{ schedule.id > 0 ? '' : ''}}</div>
                    <div></div>
                </div>
                
            </q-card-section>
            <q-separator />
            <q-card-section class="col q-pt-none">
                <div style="width: 100%;">
                    <q-select dense v-model="schedule.classification" :options="classification_list" label="" />
                    <q-input dense label="  *" v-model="schedule.title" label-color="red" @keyup.enter="onSave" />
                    <q-input v-if="isDetail" dense label=" " v-model="schedule.body" type="textarea" 
                        placeholder="500  ." />
                    
                    <q-input dense label=" " v-model="schedule.location" @keyup.enter="onSave" />
                    <div style="display: flex; align-items: center; justify-content: space-between;">
                        <div style="display: flex;">
                            <template v-if="schedule.isAllday == false">
                                <q-input outline dense v-model="schedule.start" style="width: 200px;" class="q-mr-sm">
                                    <template v-slot:prepend>
                                        <q-icon name="event" class="cursor-pointer">
                                            <q-popup-proxy cover transition-show="scale" transition-hide="scale">
                                                <q-date v-model="schedule.start" mask="YYYY-MM-DD HH:mm">
                                                    <div class="row items-center justify-end">
                                                        <q-btn v-close-popup label="Close" color="primary" flat />
                                                    </div>
                                                </q-date>
                                            </q-popup-proxy>
                                        </q-icon>
                                    </template>
        
                                    <template v-slot:append>
                                        <q-icon name="access_time" class="cursor-pointer">
                                            <q-popup-proxy cover transition-show="scale" transition-hide="scale">
                                                <q-time v-model="schedule.start" mask="YYYY-MM-DD HH:mm" format24h>
                                                    <div class="row items-center justify-end">
                                                        <q-btn v-close-popup label="Close" color="primary" flat />
                                                    </div>
                                                </q-time>
                                            </q-popup-proxy>
                                        </q-icon>
                                    </template>
                                </q-input>
                                <q-input outline dense v-model="schedule.end" style="width: 200px;">
                                    <template v-slot:prepend>
                                        <q-icon name="event" class="cursor-pointer">
                                            <q-popup-proxy cover transition-show="scale" transition-hide="scale">
                                                <q-date v-model="schedule.end" mask="YYYY-MM-DD HH:mm">
                                                    <div class="row items-center justify-end">
                                                        <q-btn v-close-popup label="Close" color="primary" flat />
                                                    </div>
                                                </q-date>
                                            </q-popup-proxy>
                                        </q-icon>
                                    </template>
        
                                    <template v-slot:append>
                                        <q-icon name="access_time" class="cursor-pointer">
                                            <q-popup-proxy cover transition-show="scale" transition-hide="scale">
                                                <q-time v-model="schedule.end" mask="YYYY-MM-DD HH:mm" format24h>
                                                    <div class="row items-center justify-end">
                                                        <q-btn v-close-popup label="Close" color="primary" flat />
                                                    </div>
                                                </q-time>
                                            </q-popup-proxy>
                                        </q-icon>
                                    </template>
                                </q-input>
                            </template>
                            <template v-else>
                                <q-input dense outline v-model="schedule.start" class="q-mr-sm">
                                    <template v-slot:append>
                                        <q-icon name="event" class="cursor-pointer">
                                            <q-popup-proxy cover transition-show="scale" transition-hide="scale">
                                                <q-date v-model="schedule.start" mask="YYYY-MM-DD">
                                                    <div class="row items-center justify-end">
                                                        <q-btn v-close-popup label="Close" color="primary" flat />
                                                    </div>
                                                </q-date>
                                            </q-popup-proxy>
                                        </q-icon>
                                    </template>
                                </q-input>
                                <q-input dense outline v-model="schedule.end">
                                    <template v-slot:append>
                                        <q-icon name="event" class="cursor-pointer">
                                            <q-popup-proxy cover transition-show="scale" transition-hide="scale">
                                                <q-date v-model="schedule.end" mask="YYYY-MM-DD">
                                                    <div class="row items-center justify-end">
                                                        <q-btn v-close-popup label="Close" color="primary" flat />
                                                    </div>
                                                </q-date>
                                            </q-popup-proxy>
                                        </q-icon>
                                    </template>
                                </q-input>
                            </template>
                        </div>
                        <div>
                            <q-checkbox v-model="schedule.isAllday" label=" " @update:model-value="onChangeAllDay" />
                        </div>
                    </div>
                    <div v-if="isDetail">
                        <q-input clearable  dense v-model="attend" label="" @keyup.enter="onAddAttend" />
                        <q-list>
                            <q-item v-for="row,idx in schedule.attendees" :key="idx">
                                <q-item-section>
                                    <q-chip :label="row" removable @remove="onRemoveAttend(idx)" />            
                                </q-item-section>
                            </q-item>
                        </q-list>
                    </div>
                </div>
            </q-card-section>
            <q-space></q-space>
            <q-card-section align="right">
                <q-btn outline :label="' ' + (isDetail?'':'')" class="q-mr-sm" @click="isDetail=!isDetail" />
                <q-btn outline color="negative" label="" class="q-mr-sm" v-if="schedule.id > 0" @click="onDelete" />
                <q-btn outline color="positive" label="" v-if="schedule.id > 0" @click="onSave" />
                <q-btn outline color="positive" label="" v-else @click="onAdd" />
            </q-card-section>
        </q-card>
    </q-dialog>
</template>

<script>
import axios from 'axios';

export default {
    name: 'dialog_scheduled',
    data() {
        return {
            position: 'right',
            isOpen: false,
            isDetail: false,
            callback: null,
            attend: '',
            classification_list: [
                { label: '', value: 'default', color: 'black', },
                { label: '', value: 'friend', color: 'green', },
                { label: '', value: 'family', color: 'pink', },
            ],
            schedule: {
                id: 0,                          //  ID
                calendarId: '',                 //  ID
                classification: 'default',      // 
                title: '',                      //  
                body: '',                       //  
                isAllday: false,                //   
                start: '',                      //   .
                end: '',                        //   
                location: '',                   //  
                attendees: [],                  //   
                category: 'time',               //  . milestone, task, allday, time  .
                dueDateClass: '',               // task  .   .
                isVisible: true,                //   
                isPending: false,	            //   
                isFocused: false,	            //   
                isReadOnly: false,	            //    
                isPrivate: false,	            //   
                color: '#000',	                //    
                backgroundColor: '#a1b56c',	    //    
                dragBackgroundColor: '#a1b56c',	//      
                borderColor: '#000',     	    //     
                // customStyle: {},	            //    . CSS     .
            },
            formError: {},
        }
    },
    methods: {
        setSchedule(schedule) {
            let vm = this;
            vm.schedule = schedule;
            vm.schedule.classification = '';
            vm.schedule.start = vm.$c.formatDate(schedule.start);
            vm.schedule.end = vm.$c.formatDate(schedule.end);
        },

        onRemoveAttend(idx) {
            let vm = this;
            vm.schedule.attendees.splice(idx, 1);
        },
        onAddAttend() {
            let vm = this;
            let text = vm.attend;
            vm.schedule.attendees.push(text);
            vm.attend = '';
        },  
        onChangeAllDay(args) {
            let vm = this;
            vm.$nextTick(() => {
                //    : YYYY-MM-DD
                if(args == true) {
                    vm.schedule.start = vm.schedule.start.slice(0, 11);
                    vm.schedule.end = vm.schedule.end.slice(0, 11);
                } 
                //    : YYYY-MM-DD HH:mm:ss
                else {
                    vm.schedule.start = vm.schedule.start + " 00:00:00";
                    vm.schedule.end = vm.schedule.end + " 00:00:00";
                }
            });
        },
        onClose() {
            let vm = this;
            vm.isOpen = false;
            if(vm.callback) { vm.callback(); }
        },
        
        open(mode, scheduleData, callback) {
            let vm = this;
            vm.mode = mode;
            vm.setSchedule(scheduleData);
            if(callback) { vm.callback = callback; }
            vm.isOpen = true;
        },

        onAdd() {
            let vm = this;
            vm.$q.loading.show();
            let coupleInfoId = vm.$store.state.user.coupleInfoId;
            if(!vm.schedule.title) {
                vm.schedule.title = "()";
            }
            axios.post(`/api/schedules/couple/${coupleInfoId}`, {
                params: {
                    schedule: vm.schedule,
                }
            }).then((res) => {
                let data = res.data;
                if(data.success) {
                    vm.$q.notify({
                        message: data.message,
                        color: 'black',
                        position: 'bottom',
                    });
                    vm.callback(vm.schedule);
                } else {
                    if(Object.prototype.hasOwnProperty.call(data, "error") == true) {
                        vm.$store.setError(vm.formError, data.error);
                    }
                }
                vm.$q.loading.hide();
                vm.isOpen = false;
            });
        },
        onSave() {
            let vm = this;
            vm.$q.loading.show();
            let scheduleId = vm.schedule.id;
            axios.put(`/api/schedules/${scheduleId}`, {
                params: {
                    schedule: vm.schedule,
                }
            }).then((res) => {
                let data = res.data;
                if(data.success) {
                    vm.$q.notify({
                        message: data.message,
                        color: 'black',
                        position: 'bottom',
                    });
                    vm.callback(vm.schedule, 'edit');
                } else {
                    if(Object.prototype.hasOwnProperty.call(data, "error") == true) {
                        vm.$store.setError(vm.formError, data.error);
                    }
                    if(Object.prototype.hasOwnProperty.call(data, "message") == true) {
                        alert(data.message);
                    }
                }
                vm.$q.loading.hide();
                vm.isOpen = false;
            });
        },
        onDelete() {
            let vm = this;
            vm.$q.loading.show();
            let scheduleId = vm.schedule.id;
            axios.delete(`/api/schedules/${scheduleId}`, {}).then((res) => {
                let data = res.data;
                if(data.success) {
                    vm.callback(vm.schedule, 'delete');
                } else {
                    if(Object.prototype.hasOwnProperty.call(data, "error") == true) {
                        vm.$store.setError(vm.formError, data.error);
                    }
                    if(Object.prototype.hasOwnProperty.call(data, "message") == true) {
                        alert(data.message);
                    }
                }
                vm.$q.loading.hide();
                vm.isOpen = false;
            });
        },
    },
    mounted() {
        let vm = this;
        
    }
}
</script>